.MuiAppBar-positionSticky{
    background-color: #f7f8bc !important;
    padding: 0 2vh;
}
.MuiDrawer-paper{
    background: #f7f8bc !important; 

}

.robot-off{
    transform: rotate(0deg);
    transition: 1s;
}

.robot-on{
    transform: rotate(90deg);
    transition: 1s;
}
.robot-jump{
    transform: rotate(360deg);
    transition: 1s;
}
